.footer{
    display:flex;
    height: 35vh;
    width: auto;
    justify-content: space-between;
    align-items: center;
    background-color:sandybrown;
    padding: 0px 10vw;
    color:white;
}

.list_1 {
   
   width: 400px;
   height: auto;
   
}

.list_2{
 padding-top: 10px;

}

.list_3{
    padding-left: 100px;
}
@media all and (max-width: 800px) {
    .footer{
        display:block;
        height: auto;
    }
    .list_1{
        padding: 20px;
        width: auto;
    }
    .list_2{
        padding: 10px;
    }
    .list_3{
        padding: 20px;
        padding-bottom: 30px;
    }
  }