.main{
    display: flex;
    height: 100vh;
    justify-content: center;
    align-items: center;
     
}   

.item2 img{
    height: 400px;
    width: auto;
    border-radius: 50%;
    border: 12px solid sandybrown;
   
}

.intro{
    
    width: 40vw;
    text-align: center;
    
  
    
}
.intro h1{
   font-size: 5em;
   font-weight: lighter;
    font-family: 'Roboto', sans-serif;
    
}
.intro p{
   font-size: 1.1em;
   font-weight: lighter;
   padding: 2px;
    /* font-family: 'Roboto', sans-serif;  */
}




@media all and (max-width: 800px){
  .main{
    margin-top: 100px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    margin-bottom: 22px;
  }
  .item2 img{
    height: 300px;
    width: auto;
    border-radius: 50%;
    border: 12px solid sandybrown;
   
}

}
