.education1{
    height: 80vh;
    display: flex;
    flex-direction:column;
    justify-content: center;
    align-items: center;
    
}
.education1 h2{
    padding: 10px 0px;  
}
.heading{
    padding: 10px;
    background-color:sandybrown;
    width: 600px;
    color: white;
}
.division{
    width: 600px;
    height: auto;
   padding: 10px 0px;
}
@media all and (max-width: 800px) {
    .division{
        width:auto;
       
    }
    .heading{
        width: auto;
    }
    .education1 h2{
        margin-top: 60px; 
    }
}